import s from "../style/accountForm.module.sass"
import { useIdentity } from "../hooks/identity";
import { DropdownItem } from '../components/MultilevelMenu';
import { FaPlus, FaCheck, 
    FaSkull, FaBolt, FaClock } from "react-icons/fa";

export function AccountPicker() {
    var [id, axios] = useIdentity();

    function onClick() {

    }

    function icon(idx) {
        if (id.users[idx].error) {
            switch(id.users[idx].error.icon) {
                case "bolt":
                    return <FaBolt/>;
                case "time":
                    return <FaClock/>;
                case "death":
                    return <FaSkull/>;
            }
        } else if (idx === id.active) {
            return <FaCheck/>;
        }
    }

    return <>
        {id.users.map((user, idx)=> {
            return (
            <DropdownItem 
                text={user.username} 
                rightIcon={icon(idx)} 
                key = {idx}
                onClick={() => {
                    if (id.active === idx) {
                        return id.setActive(null);
                    }       
                    id.setActive(idx)
                }}/>
        );})}
        <DropdownItem leftIcon={<FaPlus/>} text="Add Acount"/>
    </>;
    
}
